<template>
  <div id="chart">
    <v-row align="center" justify="center" class="ml-0 mr-0 " >
      <v-col cols="4" style="text-align: -webkit-center;">
          <div id="chart">
            <v-card  class="center " height="265px">
            <apexchart class="zoomm" type="donut" height="255px" :options="chartOptionsPowerfactor" :series="seriesPowerfactor"></apexchart>
            </v-card>
          </div>
      </v-col>

      <v-col cols="4" style="text-align: -webkit-center;">
          <div id="chart">
             <v-card  class="center" height="265px">
                <apexchart class="zoomm" type="donut" height="255px" :options="chartOptionsFrequency" :series="seriesFrequency"></apexchart>
            </v-card>
          </div>
      </v-col>

      <v-col cols="4" style="text-align: -webkit-center;">
          <div id="chart">
            <v-card  class="center" height="265px">
            <apexchart class="zoomm" type="donut" height="255px" :options="chartOptionsEnergy" :series="seriesEnergy"></apexchart>
            </v-card>
          </div>
      </v-col>
    </v-row>
    <!-- {{this.$store.state.data_Donut.pf0}} -->
  </div>
</template>
<script>
import Swal from "sweetalert2";
export default {
  data() {
    return {
      result: "",
      machineName: "",
      machineID: "",
      department: "",
      seriesPowerfactor: [],
      seriesFrequency: [],
      seriesEnergy: [],
      resultcheck: "",
      total:0,
      chartOptionsFrequency: {
        chart: {
          width: 380,
          type: "donut",
          dropShadow: {
            enabled: false,
            color: "#111",
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          }
        },
        stroke: {
          width: 0
        },
        plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
                // total: {
                //   show: true,
                //   label: "Total",
                //   color: "#373d3f",
                //   fontSize: "16px",
                //   fontFamily: undefined,
                //   fontWeight: 600,
                //   formatter: function(w) {
                //     return w.globals.seriesTotals.reduce((a, b) => {
                //       return a + b + 0.01;
                //     }, 0.1);
                //   }
                // }
              }
            }
          }
        },
         fill: {
              type: 'gradient',
            },
        dataLabels: {
          enabled: false
        },
        labels: ["f0", "f1" + " .", "f2"],
        states: {
          hover: {
            filter: "none"
          }
        },
        colors: ["#BBDEFB", "#64B5F6", "#1E88E5"],
        title: {
          text: "Frequency"
        },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: "bottom"
              }
            }
          }
        ]
      },
      chartOptionsEnergy: {
        chart: {
          width: 380,
          type: "donut",
          dropShadow: {
            enabled: false,
            color: "#111",
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          }
        },
        stroke: {
          width: 0
        },
        plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
                total: {
                  show: true,
                  label: "Total",
                  color: "#373d3f",
                  fontSize: "16px",
                  fontFamily: undefined,
                  fontWeight: 600,
                  formatter: function(w) {
                    return w.globals.seriesTotals.reduce((a, b) => {
                    return a+b;
                    }, 0);
                  }
                }
              },
              
            }
          }
        },
        labels: ["e0", "e1" + " .", "e2"],
        states: {
          hover: {
            filter: "none"
          }
        },
        colors: ["#F8BBD0", "#F06292", "#D81B60"],
        title: {
          text: "Energy"
        },
        fill: {
              type: 'gradient',
            },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: "bottom"
              }
            }
          }
        ]
      }
    };
  },
  created() {
    this.getdata_storage();
    this.getdata_device();
  },
  computed: {
   chartOptionsPowerfactor() {
     return {
         chart: {
          width: 300,
          type: "donut",
          
          responsive: [{
              breakpoint: 480,
              options: {
                chart: {
                  width: 200
                },
                legend: {
                  position: 'bottom'
                }
              }
            }],
          dropShadow: {
            enabled: false,
            color: "#111",
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          }
        },
        stroke: {
          width: 0
        },
          plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
              }
            }
          }
        },
        labels: ["pf0", "pf1" + " .", "pf2"],

        states: {
          hover: {
            filter: "none"
          }
        },
        colors: ["#69F0AE", "#00E676", "#00C853"],
        title: {
          text: "Power factor"
        },
        dataLabels: {
          enabled: false
        },
        fill: {
              type: 'gradient',
            },
        // dataLabels: {
        //   enabled: true,
        //   textAnchor: 'start',
        //   style: {
        //     colors: ['#fff']
        //   },
        //   formatter: function(value, { seriesIndex = 0, dataPointIndex, w }) {
        //     return w.config.series[seriesIndex].name + ":  " + value
        //   },
        //   offsetX: 0,
        //   dropShadow: {
        //     enabled: true
        //   }
        // },
     }
    },
  },
  methods: {
    getdata_device() {
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/gauge/" + this.machineID,
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(
        response => {
          this.loading = false;
          this.resultcheck = response.data;
          this.result = response.data.result;  
          // console.log(this.result);
          if (this.resultcheck["message"] === "success") {
            this.$store.commit("chartOptionsPowerfactor",this.result);

            var cut_pf0 = parseFloat(this.result.pf0.toFixed(2));
            var cut_pf1 = parseFloat(this.result.pf1.toFixed(2));
            var cut_pf2 = parseFloat(this.result.pf2.toFixed(2));
            this.seriesPowerfactor.push(cut_pf0, cut_pf1, cut_pf2);
            var cut_f0 = parseFloat(this.result.f0.toFixed(2));
            var cut_f1 = parseFloat(this.result.f1.toFixed(2));
            var cut_f2 = parseFloat(this.result.f2.toFixed(2));
            this.seriesFrequency.push(cut_f0, cut_f1, cut_f2);
            var cut_E0 = parseFloat(this.result.E0.toFixed(2));
            var cut_E1 = parseFloat(this.result.E1.toFixed(2));
            var cut_E2 = parseFloat(this.result.E2.toFixed(2));

            this.seriesEnergy.push(cut_E0, cut_E1, cut_E2);
          } else {
            this.seriesPowerfactor = [0, 0, 0];
            this.seriesFrequency = [0, 0, 0];
            this.seriesEnergy = [0, 0, 0];
          }
        }
      ).catch(error => {
          if (error.response.data.error_message === "Measurement Not Found") {
            this.detdata_default_power();
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "ไม่พบข้อมูล"
            });
          } else if (error.response.statusText === "UNAUTHORIZED") {
            this.$message.warning('Time out !!!');
            this.$router.push("/login");
          }else if (error.response.statusText === "UNPROCESSABLE ENTITY") {
            this.$message.warning('Please sign in again');
            this.$router.push("/login");
          }else {
            this.detdata_default_power();
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "กรุณาตรวจสอบการเชื่อมต่อ"
            });
          }
        });
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("arrayDevice"));
      if (data === null) {
        this.array_getdata = [];
      } else {
        this.array_getdata = data;
      }
      this.machineName = this.array_getdata[0].machineName;
      this.machineID = this.array_getdata[0].machineID;
      this.department = this.array_getdata[0].department;
    }
  }
};
</script>
<style scoped>
.zoomm {
  zoom: 91%;
}
</style>